.ce-icon.ce-icon__product {
  align-items: center;
  background: linear-gradient(317.58deg, #6645ad 0%, #2d71be 100%);
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
}
.ce-icon.ce-icon__product.ce-icon--small {
  height: 5rem;
  width: 5rem;
}
.ce-icon.ce-icon__product.ce-icon--small svg {
  height: calc(5rem - 40%);
  margin: 0 auto;
  width: calc(5rem - 40%);
}
.ce-icon.ce-icon__product.ce-icon--large {
  height: 7.5rem;
  width: 7.5rem;
}
.ce-icon.ce-icon__product.ce-icon--large svg {
  height: calc(7.5rem - 40%);
  margin: 0 auto;
  width: calc(7.5rem - 40%);
}
.ce-icon.ce-icon__product.ce-icon--jumbo {
  height: 10rem;
  width: 10rem;
}
.ce-icon.ce-icon__product.ce-icon--jumbo svg {
  height: calc(10rem - 40%);
  margin: 0 auto;
  width: calc(10rem - 40%);
}
.ce-icon.ce-icon__product .ce-icon--white {
  fill: #fff;
}
.ce-icon.ce-icon__secondary {
  align-items: center;
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
}
.ce-icon.ce-icon__secondary.ce-icon--small {
  height: 3rem;
  width: 3rem;
}
.ce-icon.ce-icon__secondary.ce-icon--small svg {
  height: calc(3rem - 40%);
  width: calc(3rem - 40%);
}
.ce-icon.ce-icon__secondary.ce-icon--small span {
  color: white;
  font-size: calc(3rem - 90%);
  font-weight: bold;
}
.ce-icon.ce-icon__secondary.ce-icon--large {
  height: 3.6rem;
  width: 3.6rem;
}
.ce-icon.ce-icon__secondary.ce-icon--large svg {
  height: calc(3.6rem - 40%);
  width: calc(3.6rem - 40%);
}
.ce-icon.ce-icon__secondary.ce-icon--large span {
  color: white;
  font-size: calc(3.6rem - 90%);
  font-weight: bold;
}
.ce-icon.ce-icon__secondary.ce-icon--jumbo {
  height: 5rem;
  width: 5rem;
}
.ce-icon.ce-icon__secondary.ce-icon--jumbo svg {
  height: calc(5rem - 40%);
  width: calc(5rem - 40%);
}
.ce-icon.ce-icon__secondary.ce-icon--jumbo span {
  color: white;
  font-size: calc(5rem - 90%);
  font-weight: bold;
}
.ce-icon.ce-icon__secondary.ce-icon--primary {
  background: linear-gradient(317.58deg, #6645ad 0%, #2d71be 100%);
}
.ce-icon.ce-icon__secondary.ce-icon--secondary {
  background: linear-gradient(317.58deg, #62248d 0%, #c4356e 100%);
}
.ce-icon.ce-icon__secondary .ce-icon--white {
  stroke: #fff;
}
.ce-icon.ce-icon__system {
  stroke: #72828d;
}
.ce-icon.ce-icon__system.ce-icon--small {
  height: 1.2rem;
  width: 1.2rem;
}
.ce-icon.ce-icon__system.ce-icon--small svg {
  height: 1.2rem;
  width: 1.2rem;
}
.ce-icon.ce-icon__system.ce-icon--large {
  height: 1.6rem;
  width: 1.6rem;
}
.ce-icon.ce-icon__system.ce-icon--large svg {
  height: 1.6rem;
  width: 1.6rem;
}
.ce-icon.ce-icon__system.ce-icon--jumbo {
  height: 1.6rem;
  width: 1.6rem;
}
.ce-icon.ce-icon__system.ce-icon--jumbo svg {
  height: 1.6rem;
  width: 1.6rem;
}
.ce-icon.ce-icon__system .ce-icon--primary {
  stroke: #00b0ff;
}
.ce-icon.ce-icon__system .ce-icon--secondary {
  stroke: #982d7c;
}
.ce-icon.ce-icon__system .ce-icon--black {
  stroke: #000;
}
.ce-icon.ce-icon__system .ce-icon--inverted {
  stroke: #fff;
}

