.ce-alert {
  padding: 1.4rem 2rem;
  border-radius: 0.5rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.ce-alert .ce-alert__icon {
  margin-right: 1rem;
}
.ce-alert .ce-alert__text {
  flex-grow: 1;
}
.ce-alert .ce-alert__link .ce-alert__link-text {
  float: right;
  font-size: 12px;
  font-weight: bold;
}
.ce-alert .ce-alert__close {
  border-radius: 0.5rem;
  margin-left: 1rem;
  -webkit-appearance: none;
  -moz-appearance: none;
  border: none;
}
.ce-alert.ce-alert--info {
  background-color: #4dc7ff;
}
.ce-alert.ce-alert--info .ce-alert__close {
  background-color: #4dc7ff;
}
.ce-alert.ce-alert--warning {
  background-color: #fca635;
}
.ce-alert.ce-alert--warning .ce-alert__close {
  background-color: #fca635;
}
.ce-alert.ce-alert--error {
  color: #fff;
  background-color: #cc0043;
}
.ce-alert.ce-alert--error .ce-alert__close {
  background-color: #cc0043;
}
.ce-alert.ce-alert--success {
  background-color: #acd84f;
}
.ce-alert.ce-alert--success .ce-alert__close {
  background-color: #acd84f;
}

