/* Checked/Unchecked Disabled Radio,
 * Checked/Unchecked Disabled Checkbox,
 * Disabled Select,
 * Checked/Unchecked Disabled Switch
 */
.ce-checkbox {
  user-select: none;
}
.ce-checkbox input {
  opacity: 0;
}
.ce-checkbox input + label {
  color: rgba(0, 0, 0, 0.6);
  cursor: pointer;
  display: inline-block;
  height: 2rem;
  line-height: 2rem;
  padding: 0.8rem 4.4rem;
}
.ce-checkbox input + label::before {
  background-color: transparent;
  border: 0.75rem solid transparent;
  border-radius: 50%;
  box-sizing: border-box;
  content: '';
  height: 3.6rem;
  left: 0;
  margin: -0.8rem;
  margin-left: 2rem;
  position: absolute;
  width: 3.6rem;
}
.ce-checkbox input + label::after {
  background-color: #fbfbfb;
  border-radius: 0.3rem;
  box-shadow: -1px 0 #b2b2b2, 1px 0 #b2b2b2, 0 -1px #b2b2b2, 0 1px #b2b2b2;
  box-sizing: border-box;
  content: '';
  height: 2rem;
  left: 0;
  margin-left: 2.8rem;
  position: absolute;
  width: 2rem;
}
.ce-checkbox input + label:hover::before {
  background-color: #e5e5e5;
  border: 0;
}
.ce-checkbox input + label:hover::after {
  box-shadow: none;
}
.ce-checkbox input + label:active::before {
  background-color: #b3b3b3;
  border: 0;
}
.ce-checkbox input + label:active::after {
  box-shadow: none;
}
.ce-checkbox input + label:focus::before {
  background-color: #ccc;
  border: 0;
}
.ce-checkbox input + label:focus::after {
  box-shadow: none;
}
.ce-checkbox input:checked + label::after {
  background-color: #192344;
  border: 0.5rem solid #fff;
  box-shadow: -1px 0 #192344, 1px 0 #192344, 0 -1px #192344, 0 1px #192344;
}
.ce-checkbox input:checked + label:hover::before {
  background-color: #e6f7ff;
}
.ce-checkbox input:checked + label:hover::after {
  box-shadow: none;
}
.ce-checkbox input:checked + label:active::before {
  background-color: #b2e7ff;
}
.ce-checkbox input:checked + label:active::after {
  box-shadow: none;
}
.ce-checkbox input:checked + label:focus::before {
  background-color: #ccefff;
}
.ce-checkbox input:checked + label:focus::after {
  box-shadow: none;
}
.ce-checkbox.ce-checkbox--disabled {
  pointer-events: none;
}
.ce-checkbox.ce-checkbox--disabled input:checked + label::before {
  background-color: #aeaeae;
}
.ce-checkbox.ce-checkbox--disabled input:checked + label::after {
  background-color: #aeaeae;
  box-shadow: -1px 0 #aeaeae, 1px 0 #aeaeae, 0 -1px #aeaeae, 0 1px #aeaeae;
}
.ce-checkbox.ce-checkbox--error input + label {
  color: #e5004b;
}
.ce-checkbox.ce-checkbox--error input:checked + label::after {
  background-color: #e5004b;
  box-shadow: -1px 0 #e5004b, 1px 0 #e5004b, 0 -1px #e5004b, 0 1px #e5004b;
}
.ce-checkbox.ce-checkbox--error input:checked + label:hover::before {
  background-color: #ffe5ed;
  border: 0;
}
.ce-checkbox.ce-checkbox--error input:checked + label:hover::after {
  box-shadow: none;
}
.ce-checkbox.ce-checkbox--error input:checked + label:active::before {
  background-color: #ffb3cc;
  border: 0;
}
.ce-checkbox.ce-checkbox--error input:checked + label:active::after {
  box-shadow: none;
}
.ce-checkbox.ce-checkbox--error input:checked + label:focus::before {
  background-color: #ffccdc;
  border: 0;
}
.ce-checkbox.ce-checkbox--error input:checked + label:focus::after {
  box-shadow: none;
}
.ce-checkbox.ce-checkbox--error .ce-errorbox {
  margin-left: 2.75rem;
  max-width: calc(100% - 2.75rem);
}
.ce-checkbox + .ce-checkbox {
  margin-top: 0.4rem;
