/* Grayscale
 * #fff - #000 in [various]% steps
 * See https://talloux.invisionapp.com/console/Design-System-ck70x3ivn2cy2017xjpu68gy4/ck70x4duz0046013ojf58m5ig/inspect
 */
/* Checked/Unchecked Disabled Radio,
 * Checked/Unchecked Disabled Checkbox,
 * Disabled Select,
 * Checked/Unchecked Disabled Switch
 */
.ce-switch {
  height: 3.8rem;
}
.ce-switch input {
  opacity: 0;
  position: absolute;
}
.ce-switch input + label {
  cursor: pointer;
  display: inline-block;
  height: 2.9rem;
  margin-left: 5.8rem;
  padding-top: 0.9rem;
  position: relative;
}
.ce-switch input + label span {
  display: inline-block;
  margin-left: 0.9rem;
  margin-top: -0.2rem;
}
.ce-switch input + label::before {
  background-color: #7f7f7f;
  border-radius: 0.75rem;
  content: '';
  height: 1.2rem;
  left: -4.8rem;
  position: absolute;
  top: 1.2rem;
  transition: 250ms ease-in-out;
  width: 4.1rem;
}
.ce-switch input + label::after {
  background-color: #fbfbfb;
  border: 0.1rem solid #b2b2b2;
  border-radius: 50%;
  box-sizing: border-box;
  content: '';
  height: 1.8rem;
  left: -4.8rem;
  position: absolute;
  transition: 250ms ease-in-out;
  width: 1.8rem;
}
.ce-switch input + label:hover::after {
  box-shadow: -0.1rem 0 0 0.9rem #e5e5e5, 0.1rem 0 0 0.9rem #e5e5e5, 0 -0.1rem 0 0.9rem #e5e5e5, 0 0.1rem 0 0.9rem #e5e5e5;
}
.ce-switch input + label:active::after {
  box-shadow: -0.1rem 0 0 0.9rem #b3b3b3, 0.1rem 0 0 0.9rem #b3b3b3, 0 -0.1rem 0 0.9rem #b3b3b3, 0 0.1rem 0 0.9rem #b3b3b3;
}
.ce-switch input + label:focus::after {
  box-shadow: -0.1rem 0 0 0.9rem #ccc, 0.1rem 0 0 0.9rem #ccc, 0 -0.1rem 0 0.9rem #ccc, 0 0.1rem 0 0.9rem #ccc;
}
.ce-switch input:checked + label::before {
  background-color: #007ab2;
}
.ce-switch input:checked + label::after {
  background-color: #002333;
  border-color: #002333;
  left: -2.5rem;
}
.ce-switch input:checked + label:hover::after {
  box-shadow: -0.1rem 0 0 0.9rem #e6f7ff, 0.1rem 0 0 0.9rem #e6f7ff, 0 -0.1rem 0 0.9rem #e6f7ff, 0 0.1rem 0 0.9rem #e6f7ff;
}
.ce-switch input:checked + label:active::after {
  box-shadow: -0.1rem 0 0 0.9rem #b2e7ff, 0.1rem 0 0 0.9rem #b2e7ff, 0 -0.1rem 0 0.9rem #b2e7ff, 0 0.1rem 0 0.9rem #b2e7ff;
}
.ce-switch input:checked + label:focus::after {
  box-shadow: -0.1rem 0 0 0.9rem #ccefff, 0.1rem 0 0 0.9rem #ccefff, 0 -0.1rem 0 0.9rem #ccefff, 0 0.1rem 0 0.9rem #ccefff;
}
.ce-switch.ce-switch--disabled {
  pointer-events: none;
}
.ce-switch.ce-switch--disabled input + label::before {
  background-color: #e5e5e5;
}
.ce-switch.ce-switch--disabled input + label::after {
  background-color: #fbfbfb;
  border-color: #dbdbdb;
}
.ce-switch.ce-switch--disabled input:checked + label::before {
  background-color: #ccefff;
}
.ce-switch.ce-switch--disabled input:checked + label::after {
  background-color: #99dfff;
  border-color: #99dfff;
}
.ce-switch.ce-switch--error input + label span {
  color: #e5004b;
}
.ce-switch.ce-switch--error input + label::before {
  background-color: #cc0043;
}
.ce-switch.ce-switch--error input + label::after {
  border: 0.1rem solid #ff3376;
  background-color: #fff;
}
.ce-switch.ce-switch--error input + label:hover::after {
  box-shadow: -0.1rem 0 0 0.9rem #ffe5ed, 0.1rem 0 0 0.9rem #ffe5ed, 0 -0.1rem 0 0.9rem #ffe5ed, 0 0.1rem 0 0.9rem #ffe5ed;
}
.ce-switch.ce-switch--error input + label:active::after {
  box-shadow: -0.1rem 0 0 0.9rem #ffb3cc, 0.1rem 0 0 0.9rem #ffb3cc, 0 -0.1rem 0 0.9rem #ffb3cc, 0 0.1rem 0 0.9rem #ffb3cc;
}
.ce-switch.ce-switch--error input + label:focus::after {
  box-shadow: -0.1rem 0 0 0.9rem #ffccdc, 0.1rem 0 0 0.9rem #ffccdc, 0 -0.1rem 0 0.9rem #ffccdc, 0 0.1rem 0 0.9rem #ffccdc;
}
.ce-switch.ce-switch--error input:checked + label::after {
  background-color: #cc0043;
  border-color: #cc0043;
}
